.home_content {
  width: 100%;
  min-height: 80vh;
  background-color: #ebebeb;
}

.loading_data {
  background-color: #1f1f1f;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: #ebebeb;
}

.cover_bg_image {
  z-index: -1;
  top: 0;
  width: 100%;
  height: 400px;
  box-sizing: border-box;
}

.cover_bg_image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile_image {
  box-sizing: border-box;
}

.profile_image img {
  border-radius: 100%;
  border: 1px solid #ebebeb;
  box-shadow: rgba(0, 0, 0, 0.25) 1px 1px 10px 1px;
  width: 200px;
  position: absolute;
  top: 300px;
  left: 100px;
}

.profile_info {
  text-align: justify;
  width: 100%;
  padding-top: 120px;
  background-color: #ebebeb;
}

.profile_info h1,
p,
h2 {
  padding: 0px 100px;
  margin: 0;
}

.home_buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  background-color: #ebebeb;
}

.profile_info h1 {
  font-weight: 600;
  margin-bottom: 20px;
}

.profile_info h2 {
  padding: 20px 0px;
  font-weight: normal;
  text-align: center;
}

.profile_info p {
  text-indent: 3em;
  margin-bottom: 20px;
  font-size: 1em;
}

.profile_info p span {
  font-weight: bolder;
  text-transform: uppercase;
}

.technologies {
  padding: 30px 0px 50px;
  background-color: #1f1f1f;
  box-shadow: #1f1f1f 1px 1px 10px 1px;
  color: #ebebeb;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.technologies div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 30px;
}

@media screen and (max-width: 500px) {
  .technologies {
    padding: 30px 0px;
  }

  .technologies h2 {
    padding: 0px 0px 30px;
  }

  .home_buttons {
    padding: 30px 0px;
    flex-direction: column;
    gap: 20px;
  }

  .profile_image {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .profile_image img {
    left: auto;
  }

  .profile_info h1,
  p,
  h2 {
    padding: 0px 20px;
  }
}